<template>
  <div>
    <swiper :options="options" class="swiper-box">
      <swiper-slide v-for="item in imgs" :key="item.pic">
        <div class="demo">
          <img :src="item.pic" />
        </div>
      </swiper-slide>
      <!-- <div class="swiper-pagination" slot="pagination"></div> -->
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";
export default {
  props: {
    imgs: Array,
  },
  data() {
    return {
      options: {
        autoplay:{   
          disableOnInteraction: false,
        },
        pagination: { el: ".swiper-pagination" },
        effect : 'flip',
      },
    };
  },
  components: {
    Swiper,
    SwiperSlide,
  },
};
</script>

<style lang="less" scoped>
.swiper-box {
  // display: block;
}
.swiper-container {
  flex:0.3;
  width: 100%;
  height: 37vw;
}
.demo {
  width: 100%;
  height: 37vw;
  img {
    width: 100%;
  }
}
</style>
