<template>
  <div class="art-sec">
    <router-link
      class="art"
      :to="`/artist/${arid}`"
      :artist="artist"
      :artistid="artistid"
    >
      <div class="left">
        <img :src="artist.cover" alt="" />
      </div>
      <div class="mid">
        歌手：<span>{{ artist.name }}</span>
      </div>
      <div class="right"></div>
    </router-link>
  </div>
</template>

<script>
export default {
  props: {
    arid: Number,
  },
  data() {
    return {
      artist: {},
      artistid: null,
    };
  },
  created() {
    this.getres();
  },
  methods: {
    aridget(value) {
      this.artistid = value;
    },
    async getres() {
      if (this.arid == null) {
        console.log("id未传达");
      } else if (this.arid != null) {
        await this.axios
          .get(`https://apis.netstart.cn/music/artist/detail?id=${this.arid}`)
          .then((res) => {
            console.log(res.data);
            this.artist = res.data.artist;
            this.artistid = this.arid;
          });
      }
    },
  },
};
</script>

<style lang="less" scoped>
.art-sec {
  // display: flex;
}
.art {
  // flex: 0.45;
  display: flex;
  margin-top: 10px;
  padding: 9px 10px;
  color: #333;
  border-bottom: 1px solid rgb(185, 185, 185);
  .left {
    line-height: 0;
    position: relative;
    flex: 0.2;
    width: 50px;
    height: 50px;
    img {
      width: 60px;
      height: 50px;
      display: block;
    }
  }
  .mid {
    flex: 0.7;
    height: 50px;
    line-height: 50px;
    font-size: 17px;
    span {
      color: #507daf;
    }
  }
  .right {
    flex: 0.1;
    margin-top: 10px;
    margin-right: 20px;
    height: 40px;
    line-height: 40px;
    background: url("../assets/images/前往-b.png") no-repeat;
    background-size: 32px;
  }
}
</style>
