<template>
  <div class="search">
    <div class="search-box">
      <i class="fa fa-search" aria-hidden="true"></i>
      <input
        placeholder="搜索歌曲、歌手、专辑"
        v-model="keywords"
        @input="inputting"
        @keyup.enter="search"
      />
      <i class="fa fa-times-circle" v-show="showdel" @click="clearWord"></i>
    </div>
    <hr />
    <!-- <artist></artist> -->
    <component
      @play="$emit('play', $event)"
      :is="showComponent"
      :keywords="keywords"
      @changekeyword="changekeyword"
      :playMusicID="playMusicID"
      :arid="arid"
      @aridget="aridget"
    ></component> 
<!--     <div class="artist"
    path="/artist"
    ></div> -->
  </div>
</template>

<script>
import SearchHot from "@/components/SearchHot.vue";
import SearchSug from "@/components/SearchSug.vue";
import SearchResult from "@/components/SearchResult.vue";
// import Artist from '@/views/Artist.vue';

export default {
  name: "Search",
  props: {
    playMusicID: Number,
  },
  data() {
    return {
      showComponent: SearchHot,
      keywords: "",
      showdel: false,
      arid:null,
    };
  },
  methods: {
    aridget(value){
      this.arid = value
      this.aridpush()
    },
    aridpush(){
      this.$emit('aridget',this.arid)
    },
    clearWord() {
      this.keywords = "";
      this.showdel = false;
      this.showComponent = SearchHot;
    },
    search(newValue) {
      if (newValue != "" && newValue != 0) {
        this.showComponent = SearchResult;
      }
    },
    changekeyword(event) {
      this.showdel = true;
      this.keywords = event;
      this.showComponent = SearchResult;
    },
    inputting() {
      if (this.keywords == "") {
        this.showdel = false;
        this.showComponent = SearchHot;
      } else {
        this.showdel = true;
        this.showComponent = SearchSug;
      }
    },
  },
  components: {
    SearchHot,
    SearchSug,
    SearchResult,
    // Artist
  },
};
</script>

<style lang="less" scoped>
.artist {
  width: 100%;
  height: 50px;
  background: skyblue;
}
.search {
  margin-bottom: 20vh;
}
.search-box {
  margin: 15px 10px;
  background-color: #ebecec;
  border-radius: 19px;
  padding: 5px 35px;
  position: relative;
  i {
    margin-right: 10px;
    position: absolute;
    top: 50%;
    font-size: 16px;
    color: #c9c9c9;
    transform: translateY(-50%);
    &.fa-search {
      left: 13px;
    }
    &.fa-times-circle {
      right: 12px;
    }
  }
  input {
    width: 100%;
    height: 25px;
    line-height: 25px;
    font-size: 13px;
    color: #c9c9c9;
    background-color: transparent;
    border: none;
    outline: none;
    color: #333;
  }
}
.search {
  hr {
    height: 0;
    border: none;
    border: 1px solid #ebecec;
  }
}
</style>
