<template>
  <div class="home">
    <banner :imgs="imgbanners" class="f-banner"></banner>
    <list-title class="f-title">热门推荐</list-title>
    <loading v-if="recommendSongs.length == 0"></loading>
    <ul class="imgList" f-imgul>
      <img-item
        v-for="song in recommendSongs"
        :key="song.id"
        :song="song"
        path="/playlist"
      ></img-item>
    </ul>
    <list-title class="f-title">前线金曲</list-title>
    <loading v-if="recommendSongs.length == 0"></loading>
    <ul class="home-songlist f-list">
      <!-- 绑定play事件，实现反向传值，$event把事件传出去  -->
      <song-list
        @play="$emit('play', $event)"
        :songlist="newsongs"
        :shownum="false"
        :currentMusicId="currentMusicId"
        :playMusicID="playMusicID"
      ></song-list>
    </ul>
    <banner-sec :imgs="imgbanners" class="banner"></banner-sec>
    <list-title>MV</list-title>
    <loading v-if="recommendSongs.length == 0"></loading>
    <div class="video-t" v-if="mvopen">
      <video width="300" controls autoplay :src="mvurl"
      class="video-iframe"
        v-if="mvopen"
      ></video>
      <span class="closemv" @click="closemv"></span>
    </div>
    <ul class="imgList f-img">
      <mv-item
        v-for="mv in recommendMv"
        :key="mv.id"
        :song="mv"
        @openmv="openmv"
      ></mv-item>
    </ul>
    <div class="foot">
      <img src="@/assets/images/logoZ-blue.png" alt="" />
      <div class="foot-logo"></div>
      <div class="foot-english"></div>
    </div>
  </div>
</template>
<script>
import ListTitle from "@/components/ListTitle.vue";
import ImgItem from "@/components/ImgItem.vue";
import SongList from "../components/SongList.vue";
import Banner from "@/components/Banner.vue";
import BannerSec from "@/components/BannerSec.vue";
import MvItem from "@/components/MvItem.vue";

export default {
  name: "Home",
  components: {
    ListTitle,
    ImgItem,
    SongList,
    Banner,
    MvItem,
    BannerSec,
  },
  props: {
    currentMusicId: { type: Number },
    playMusicID: Number,

    // picURL:null
  },
  data() {
    return {
      recommendSongs: [], //推荐歌单数据
      newsongs: [],
      imgbanners: [],
      recommendMv: [],
      picURL: null,
      mvopen: false,
      mvurl: null,
      getmvid: null,
    };
  },
  methods: {
    closemv() {
      this.mvopen = false;
    },
    openmv(val, id) {
      console.log(id);
      this.mvopen = val;
      this.getmvid = id;
      this.$axios
        .get(`https://apis.netstart.cn/music/mv/url?id=${this.getmvid}`)
        .then((res) => {
          this.mvurl = res.data.url;
        });
    },
  },
  created() {
    this.$axios.get("/personalized?limit=6").then((result) => {
      this.recommendSongs = result;
    });
    this.$axios.get("/personalized/newsong").then((res) => {
      this.newsongs = res;
    });
    this.$axios.get("/banner?type=1").then((result) => {
      this.imgbanners = result.banners;
    });
    this.$axios
      .get("https://apis.netstart.cn/music/mv/first?limit=30")
      .then((result) => {
        this.recommendMv = result.data;
      });
  },
};
</script>
