<template>
  <div class="play-box">
    <div class="svg-box">
      <svg width="0" height="0">
        <filter id="sketch">
          <feTurbulence
            type="fractalNoise"
            baseFrequency=".05 .5"
            numOctaves="3"
            result="noise1"
          ></feTurbulence>
          <feColorMatrix
            in="noise1"
            type="hueRotate"
            values="0"
            result="noise2"
          >
            <animate
              attributeName="values"
              from="0"
              to="360"
              dur="1.5s"
              repeatCount="indefinite"
            ></animate>
          </feColorMatrix>
          <feDisplacementMap
            xChannelSelector="R"
            yChannelSelector="G"
            scale="60"
            in="SourceGraphic"
            in2="noise2"
          ></feDisplacementMap>
        </filter>
      </svg>
      <div class="svg-div"></div>
    </div>

    <div class="fullscreen">
      <div class="wei-fullscreen"></div>
      <!-- 唱片容器 -->
      <div class="record" :style="{'animation-play-state':playState?'running':'paused'}"></div>
      <img
        :src="currentMusic.picUrl"
        :alt="currentMusic.name"
        class="head-circle"
        :style="{'animation-play-state':playState?'running':'paused'}"
      />
      <span class="back" @click="$emit('hidefull', false)"></span>

      <div class="lyric">
        <ul :style="{ transform: `translateY(-${lyricIndex * 26}px)` }">
          <li
            v-for="(ly, index) in lyricArray"
            :key="index"
            :class="{ active: index == lyricIndex }"
          >
            {{ ly.lyric }}
          </li>
        </ul>
      </div>
      <div class="control">
        <input
          v-model="timeprocess"
          type="range"
          class="process"
          max="100"
          min="0"
          value="0"
        />
        <div class="btn-box">
          <span @click="$emit('changePlaying','prev')" class="last"></span>
          <span
            :class="[{ playing: !playState }, { play: playState }]"
            @click="$emit('playpause')"
          ></span>
          <span @click="$emit('changePlaying','next')" class="next"></span>
        </div>
      </div>
    </div>
    <div
      class="shadow"
      :style="{
        'background-image': `url(${currentMusic.picUrl})`,
        'background-size': 'auto 100%',
        filter: 'blur(15px)',
      }"
    ></div>
    <!-- <canvas width="600" height="600"></canvas> -->
  </div>
</template>

<script>
export default {
  props: {
    currentMusic: Object,
    currentTime: Number,
    duration: Number,
    playState: Boolean,
  },
  data() {
    return {
      lyric: "",
    };
  },
  created() {
    this.axios.get(`/lyric?id=${this.currentMusic.id}`).then((res) => {
      this.lyric = res.lrc.lyric;
    });
  },
  computed: {
    lyricArray() {
      if (this.lyric == "" || this.lyric.length < 1) return [];
      let arr = this.lyric.split("\n");
      arr.pop();
      let reg = /\[(\d+):(\d+.\d+)\](.*)/;
      let lyricArr = arr.map((ly) => {
        if (reg.test(ly)) {
          return {
            time: parseInt(RegExp.$1) * 60 + parseFloat(RegExp.$2),
            lyric: RegExp.$3,
          };
        }
      });
      return lyricArr;
    },
    lyricIndex() {
      if (this.lyricArray.length < 1) return 0;
      for (let i in this.lyricArray) {
        if (i >= this.lyricArray.length - 2) {
          return this.lyricArray.length - 1;
        } else {
          if (
            this.currentTime > this.lyricArray[i].time &&
            this.lyricArray[parseInt(i) + 1].time > this.currentTime
          ) {
            return i;
          }
        }
      }
      return 1;
    },

    timeprocess: {
      get() {
        let process = (this.currentTime / this.duration) * 100;
        return process;
      },
      set(value) {
        let ctime = (value / 100) * this.duration;
        this.$emit("changeTime", ctime);
      },
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes roll {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.shadow {
  position: fixed;
  z-index: 95;
  top: -10%;
  left: -5%;
  width: 120vw;
  height: 200vw;
  background: rgb(0, 0, 0, 0.9);
}
.fullscreen {
  position: fixed;
  z-index: 98;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  .wei-fullscreen {
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
  }
  .wei-fullscreen::after {
    content: "";
    position: absolute;
    // background: rgb(0, 0, 0,0.5);
    background: url("../assets/images/rain_03.jpg") no-repeat;
    background-size: 100%;
    opacity: 0.7;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
  }
  .back {
    float: left;
    margin: 2vw 3vh;
    width: 40px;
    height: 40px;
    background: url(../assets/images/返回4.png) no-repeat;
    background-size: 40px 40px;
  }
  .record {
    position: absolute;
    top: 16vw;
    left: 20vw;
    display: block;
    width: 56vw;
    height: 56vw;
    background: url(../assets/images/唱片02.png) no-repeat;
    background-size: 56vw;
    border-radius: 50%;
    margin: 0 auto;
    animation: roll 8s linear infinite;
  }
  .head-circle {
    position: absolute;
    top: 27vw;
    left: 30vw;
    display: block;
    width: 35vw;
    height: 35vw;
    border-radius: 50%;
    margin: 0 auto;
    animation: roll 8s linear infinite;
  }
  .lyric {
    overflow: hidden;
    position: relative;
    text-align: center;
    top: 60vw;
    height: 24vh;
    width: 90vw;
    ul {
      position: absolute;
      top: 0;
      left: 10vw;
      width: 80vw;
      transition: transform 0.3s linear;
      // background: rgba(0, 0, 0, 0.2);
      li {
        display: block;
        margin: 14px auto;
        color: white;
        font-size: 15px;
      }
      .active {
        font-size: 16px;
        font-weight: 700;
        color: rgb(71, 186, 231);
      }
    }
  }
  .control {
    width: 100%;
    height: 50px;
    .process {
      display: block;
      position: absolute;
      margin-left: 7vw;
      width: 84vw;
      top: 75vh;
      z-index: 100;
    }
    .btn-box {
      display: flex;
      margin-left: 15vw;
      width: 70vw;
      height: 50px;
      position: fixed;
      justify-content: space-around;
      top: 82vh;
      span {
        display: inline-block;
        width: 50px;
        height: 50px;
      }
      .last {
        background: url(../assets/images/上一首02.png) no-repeat;
        background-size: 50px;
      }
      .play {
        height: 40px;
        margin-top: 5px;
        margin-left: 10px;
        background: url(../assets/images/暂停01.png) no-repeat;
        background-size: 40px;
      }
      .playing {
        height: 40px;

        margin-top: 2px;
        padding-top: 3px;
        margin-left: 7px;
        padding-left: 3px;
        background: url(../assets/images/播放01.png) no-repeat;
        background-size: 45px;
      }
      .next {
        background: url(../assets/images/下一首01.png) no-repeat;
        background-size: 50px;
      }
    }
  }
}
/* canvas {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
} */
.svg-box {
  position: fixed;
  z-index: 98;
  top: 0;
  left: 0;
  width: 58vw;
  height: 50vw;
  background: rgba(0, 0, 0, 0);
  .svg-div {
    width: 55vw;
    height: 55vw;
    background-color: rgb(231, 231, 231);
    background-color: rgb(0, 225, 255);
    border-radius: 50%;
    position: absolute;
    top: calc(45% - 5vw);
    left: calc(45% - 5vw);
    -webkit-filter: url(#sketch);
    filter: url(#sketch);
    box-shadow: 0 0 0 5vw rgb(255 255 255 / 15%);
  }
}
</style>
