<template>
  <div>
    <audio
      autoplay
      :src="
        `https://music.163.com/song/media/outer/url?id=${currentMusic.id}.mp3`
      "
      ref="player"
      @play="playState = true"
      @pause="playState = false"
      @timeupdate="timeupdate"
    ></audio>
    <!-- @durationchange="durationchange" -->
    <section class="playbar">
      <img
        :src="currentMusic.picUrl"
        :style="{ 'animation-play-state': playState ? 'running' : 'paused' }"
      />
      <h3 @click="showfull = true">
        <span>
          {{ currentMusic.name }}
          <b v-for="artist in currentMusic.song.artists" :key="artist.id">
            - {{ artist.name }}</b
          >
        </span>
      </h3>
      <i class="playbtn" @click="playState = !playState">
        <!-- <button :class="['fa',{'fa-play':!playState},{'fa-pause':playState}]" aria-hidden="true"></button> -->

        <div class="progress">
          <canvas width="40" height="40" ref="canvas"></canvas>
          <div
            :class="['icon', { play: !playState }, { pause: playState }]"
            aria-hidden="true"
          ></div>
        </div>
      </i>
      <div class="list" @click="showlist = !showlist"></div>
      <div class="play-card" 
        v-if="showlist"
      >
        <span class="close" @click="showlist = false"></span>
        <h2>
          当前播放 <span>( {{ playList.length }} )</span>
        </h2>
        <p
          v-for="pl in playList"
          :key="pl.id"
          :class="{ active: pl.id == currentMusic.id }"
          @click="$emit('changePlaying', pl)"
        >
          {{ pl.name }}-{{ pl.song.artists[0].name }}
        </p>
      </div>
    </section>
    <div class="full-screen">
      <full-screen
        v-if="showfull"
        @hidefull="showfull = $event"
        :currentMusic="currentMusic"
        :currentTime="currentTime"
        :duration="duration"
        @changeTime="changeTime"
        @playpause="playState = !playState"
        :playState="playState"
        @changePlaying="$emit('changePlaying', $event)"
        :playList="playList"
      ></full-screen>
    </div>
  </div>
</template>

<script>
import FullScreen from "@/components/FullScreen.vue";
export default {
  props: {
    currentMusic: {
      type: Object,
      default: null,
    },
    playList: Array,
  },
  components: {
    FullScreen,
  },
  data() {
    return {
      playState: true,
      currentTime: 0,
      duration: 0,
      showfull: false,
      showlist: false,
      // playList
    };
  },
  methods: {
    changeTime(event) {
      this.$refs.player.currentTime = event;
    },
    timeupdate(event) {
      this.currentTime = event.target.currentTime;
      this.duration = event.target.duration;
    },
    durationchange(event) {
      this.duration = event.target.duration;
    },
  },
  mounted: function() {
    // console.log(this.$refs.canvas);
    // this.canvas = this.$refs.canvas
  },
  computed: {
    percentage: function() {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    playState(newValue) {
      if (newValue) {
        this.$refs.player.play();
      } else {
        this.$refs.player.pause();
      }
    },
    percentage: function(n) {
      // console.log(n);
      var context = this.$refs.canvas.getContext("2d");
      context.clearRect(0, 0, 40, 40);

      context.beginPath();
      context.arc(
        20,
        20,
        16,
        (Math.PI / 180) * (0 - 90),
        (Math.PI / 180) * (360 * n - 90)
      );
      context.strokeStyle = "rgb(77,77,77)";
      context.lineWidth = 2;
      context.stroke();

      context.beginPath();
      context.arc(
        20,
        20,
        16,
        (Math.PI / 180) * (360 * n - 90),
        (Math.PI / 180) * (360 - 90)
      );
      context.strokeStyle = "gray";
      context.lineWidth = 1;
      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes roll {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playbar {
  width: 100%;
  height: 50px;
  padding-bottom: 10px;
  background: white;
  position: fixed;
  bottom: 9vh;
  left: 0;
  display: flex;
  align-items: center;
  padding: 0 12px;
  z-index: 50;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);

  img {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    animation: roll 8s linear infinite;
    /*  animation: rotate 8s linear infinite;
    animation-play-state: paused; */
  }
  h3 {
    padding: 0 10px;
    flex: 0.7;
    font-size: 14px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    b {
      color: rgb(151, 151, 151);
      font-size: 10px;
    }
  }
  .playbtn {
    flex: 0.1;
  }
  .list {
    flex: 0.2;
    position: relative;
    margin-left: 10px;
    width: 25px;
    height: 25px;
    background: url("../assets/images/songlist_01.png") no-repeat;
    background-size: 25px 25px;
  }
  .play-card {
    width: 350px;
    height: 420px;
    overflow: hidden;
    position: fixed;
    // position: absolute;
    top: 20vh;
    left: 5vw;
    background: rgba(85, 84, 84, 0.9);
    border-radius: 15px;
    z-index: 60;
    .active {
      color: #3691f8;
      font-weight: 700;
      font-size: 20px;
    }
    p {
      color: white;
      width: 300px;
      margin: 10px 30px;
      font-size: 18px;
    }
    .close {
      position: absolute;
      top: 10px;
      right: 20px;
      width: 20px;
      height: 20px;
      background: url("../assets/images/关闭.png");
      background-size: 20px;
    }
    h2 {
      margin: 20px;
      font-size: 20px;
      color: white;
      span {
        font-size: 16px;
      }
    }
  }
  .progress {
    width: 30px;
    height: 30px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    canvas {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    .icon {
      box-sizing: border-box;
      width: 12px;
      height: 12px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      &.play {
        border-top: 6px solid transparent;
        border-bottom: 6px solid transparent;
        border-left: 10px solid grey;
        transform: translateX(3px);
      }
      &.pause {
        // background: lightblue;
        &::before,
        &::after {
          content: "";
          display: block;
          height: 80%;
          width: 20%;
          background: grey;
        }
      }
    }
  }
  &.playing {
    img {
      animation-play-state: running;
    }
  }
}
</style>
