<template>
  <div class="words">
      <div class="hotword">
          <span 
            v-for="word in hotWords" 
            :key="word.searchWord"
            @click="$emit('changekeyword',word.searchWord)">
            {{ word.searchWord }}
          </span>
      </div>
      <!-- <div class="history">
          搜索历史
          localstorage实现
      </div> -->
  </div>
</template>

<script>
export default {
    data(){
        return{
            hotWords:[]
        }
    },
    created(){
        this.axios.get("/search/hot/detail").then(result=>{
            this.hotWords=result.data.slice(0,10)
        })
    }
}
</script>

<style lang="less" scoped>
.hotword{
    overflow: hidden;
    margin-bottom: 20px;
    span {
        float: left;
        line-height: 16px;
        padding: 5px 10px;
        margin: 5px;
        font-size: 14px;
        color: rgb(73, 73, 73);
        border-radius: 14px;
        border: 1px solid #ddd;
    }
}
</style>