<template>
  <div id="app">
    <div class="head-banner">
      <div class="svg">
        <img src="@/assets/images/logoZ.png" alt="" />
        <span>致一音乐</span>
      </div>
      <span class="dowload-btn">下载APP</span>
    </div>
    <!-- v-if设置当前在播放时才显示播放组件 -->
    <keep-alive>
      <!-- 反向传值的终点 -->
      <router-view
      @play="play"
      :currentMusicId="currentMusic ? currentMusic.id : null"
      :playMusicID="playMusicID"
       />
    </keep-alive>
    <play v-if="currentMusic" 
    :currentMusic="currentMusic" 
    :playList="playList"
    @changePlaying="changePlaying"></play>
    <top-nav></top-nav>
  </div>
</template>

<script>
import Nav from "@/components/Nav.vue";
import Play from "@/components/Play.vue";

export default {
  data() {
    return {
      banners: [],
      currentMusic:null,
      currentMusicId:0,
      playList:[],
      coming:false,
    };
  },
  components: {
    "top-nav": Nav,
    Play,
  },
  methods: {
    play(event) {
      if(event.al&&event.al.picUrl){
        event.picUrl=event.al.picUrl;
      }
      this.currentMusic=event;  
      let isRepeat = false
      this.playList.forEach(v=>{
        if(v.id==event.id){
          isRepeat = true
        }
      })
      if(!isRepeat) this.playList.push(event)
        
    },
    changePlaying(event){
    let currentIndex=0
    this.playList.forEach((v,index)=>{
      if(v.id==this.currentMusic.id){
        currentIndex= index
      }
    })
    if(event=='prev'){
      if(currentIndex==0){
        currentIndex=this.playList.length-1
      }else {
        currentIndex--;
      }
      
    }else if(event=='next'){
      if(currentIndex==this.playList.length-1){
        currentIndex=0
      }else {
        currentIndex++;
      }
    }else if(typeof event == 'object'){
      this.currentMusic=event
      return 
    }
    this.currentMusic=this.playList[currentIndex]
  }

  },
  computed:{
    playMusicID(){
      if(this.currentMusic){
        return this.currentMusic.id
      }
      return 0
    }
  }
  
};
</script>

