<template>
  <div class="suggest">
      <h3 @click="$emit('changekeyword',keywords)">搜索"{{keywords}}"</h3>
      <loading v-show="isSearch"></loading>
      <hr>
      <ul>
        <li v-for="s in sugArray" :key="s.id" @click="$emit('changekeyword',s.keyword)">
          <i class="fa fa-search" aria-hidden="true"></i>
          <span>{{s.keyword}}</span>
          <hr>
        </li>       
      </ul>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  props:{
    keywords:String
  },
  data(){
    return {
      sugArray:[],
      isSearch:false,
    }
  },
  methods:{
    //防抖
    getSearch:_.debounce(function(value){
      this.isSearch = true;
      this.axios.get(`/search/suggest?keywords=${value}&type=mobile`)
      .then(result=>{
        this.isSearch=false;
        if(result.allMatch) this.sugArray=result.allMatch;
      });
    },200)
    // getSearch(value){
    //   this.axios.get(`/search/suggest?keywords=${value}&type=mobile`)
    //   .then(result=>{
    //     this.sugArray=result.allMatch;
    //   })
    // }

  },
  created(){
    this.getSearch(this.keywords)
  },
  watch:{
     keywords(newValue){
       this.getSearch(newValue)
    } 
  }
}
</script>

<style lang="less" scoped>
.suggest{
  padding: 10px;
  h3{
    margin-bottom: 20px;
    font-size: 14px;
    color: #507daf;
  }
  ul{
    li {
      i{
        color:#c9c9c9
      }
      span{
        margin: 10px; 
        line-height: 45px;
        font-size: 16px;
        color: rgb(61, 61, 61);
      }
    }
  }
        hr {
        height: 0;
        border:none;
        border: 1px solid #ebecec;
      }
}
</style>