<template>

    <ul class="song-list"  :style="{paddingLeft:shownum?'40px':'10px'}">
      <li class="sli" v-for="(song, index) in songlist" :key="index"  @click="$emit('play',song)">
        <!-- 前三个换色，因为过滤器+1了，所以选取小于3 -->
        <!-- 文本字数过多超出部分在left样式中限制完毕 -->
        <div v-if="shownum" :class="['shownum', { red: index < 3 }]">
          {{ index | formatOrder }}
        </div>
        <div class="left">
          <!-- <h3>{{ song.name | highKey(key)}}</h3> -->
          <h3 v-html="$options.filters.highKey(song.name,keywords)"></h3>
          <span>
          <span v-if="song.song.privilege" :class="{ 'sq-icon': song.song.privilege.maxbr == 999000 }"></span>
            <b v-for="(artist, index) in song.song.artists" :key="artist.id">
              <i v-html="$options.filters.highKey(artist.name,keywords)"></i>
              <!-- {{ artist.name}} -->
              {{ index | filterChar(song.song.artists.length) }}
            </b>
            
            <!-- - {{ song.song.album.name }} -->
            -  <i v-html="$options.filters.highKey(song.song.album.name,keywords)"></i>
            </span>
        </div>
        <i :class="{'play-btn':playMusicID!=song.id}" >
          <div :class="{'play':playMusicID!=song.id}"></div>
        </i>
      <running class="running" v-if="playMusicID==song.id"></running>
      </li>
    </ul>
</template>

<script>
export default {
  name: "SongList",
  props: {
    songlist: {
      type: Array,
        default: function() {
          return [];
        },
    },
    shownum: {
      type: Boolean,
      default: false
    },
    keywords:{
      type:String,
      default:""
    },
    currentMusicId:{
      type:Number
    },
    playMusicID:{
      type:Number
    }
  },
  filters: {
    filterChar(val, num) {
      return val < num - 1 ? "/" : "";
    },
    formatOrder(val) {
      val++;
      return val < 10 ? "0" + val : val;
    },
    highKey(val,keywords){
      let reg = new RegExp(`${keywords}`,'g')
      if(reg.test(val)){
        val = val.replace(reg,`<b style="color:#539df1">${keywords}</b>`)
      }
      return val;
    }
  },
};
</script>

<style lang="less" scoped>
.song-list {
  padding-top: 5px;
  padding-left: 30px;
  .sli {
    display: flex;
    position: relative;
    align-items: center;
    padding: 10px 10px;
    border-bottom: 1px solid #ccc;
    .running {
       flex:0.1;
      
    
    }
    div {
      &.shownum {
        left: -30px;
        padding: 0 10px;
        position: absolute;
        padding: 0px 10px;
        color: #888;
        font-size: 17px;
      }
      &.red {
        color: #539df1;
      }
    }
    div.left {
      min-width: 200px;
      white-space: nowrap;
      overflow: hidden;
      flex: 0.9;
      h3 {
        font-size: 17px;
        color: #333;
      }
      span {
        font-size: 12px;
        color: #888;
        position: relative;
        padding: 0px 0px 16px;
        .sq-icon {
          padding: 0px 0px 0px 15px;
          &::after {
            content: "";
            position: absolute;
            top: 60%;
            transform: translateY(-50%);
            left: 0px;
            width: 10px;
            height: 10px;
            background: url("../assets/images/icon.png") no-repeat;
            background-size: 166px 97px;
          }
        }
      }
    }
    i.play-btn {
      flex: 0.1;
      position: absolute;
      right: 20px;
      width: 22px;
      height: 22px;
       
      .play {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background: url("../assets/images/icon.png") no-repeat;
        background-size: 166px 98px;
        background-position: -24px 0;
     
      }
    }
    
  }
}
</style>
