<template>
  <div class="searchresult">
    <loading v-if="result.length == 0"></loading>
    <div class="sr">搜索结果:
      <div class="tg"></div>
    </div>
    <art-sec v-if="arid!=null" :arid="arid"></art-sec>
    <!-- <div class="artist"
    path="/artist"
    ></div> -->
    <song-list
      :keywords="keywords"
      :songlist="result"
      @play="$emit('play', $event)"
      :playMusicID="playMusicID"
    ></song-list> 
    <span ref="bottom" class="getmore" @click="getMore">{{
      isMore ? "loading more" : "no more"
    }}</span>
    <loading v-if="result.length == 0"></loading>
  </div>
</template>
<!-- 前三个换色，因为过滤器+1了，所以选取小于3 -->
<!-- 文本字数过多超出部分在left样式中限制完毕 -->
<!-- 搜索那边，直接检索song.artist就有artist.name -->
<script>
import SongList from "../components/SongList.vue";
import ArtSec from "../components/ArtSec.vue"
export default {
  props: {
    keywords: String,
    playMusicID: Number,
  },
  data() {
    return {
      result: [],
      page: 1,
      limit: 20,
      isloading: false,
      isMore: true,
      arid:null,
    };
  },
  components: { SongList,ArtSec },
  created() {
    this.getResult();
    // this.aridup();
  },
  mounted() {
    window.addEventListener("scroll", this.windowScroll);
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      window.addEventListener("scroll", vm.windowScroll);
    });
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.windowScroll);
  },
  beforeRouteLeave(to, from, next) {
    window.removeEventListener("scroll", this.windowScroll);
    next();
  },

  methods: {
    aridup(){
      this.$emit('aridget',this.arid)
    },

    windowScroll() {
      // console.log(window.scrollY,window.innerHeight,this.$refs.bottom.offsetTop);
      if (window.scrollY + window.innerHeight > this.$refs.bottom.offsetTop) {
        this.getMore();
      }
    },
    async getResult() {
      let offset = (this.page - 1) * this.limit;
      await this.axios
        .get(
          `http://47.115.51.185:8081/cloudsearch?keywords=${this.keywords}&limit=${this.limit}&offset=${offset}`     
        )
        .then((result) => {        
          if (result.songCount == 0) {
            this.isloading = false;
            this.isMore = false;
            return;
          }
          let re = result.songs.map((v) => {
            v.song = {
              privilege: v.privilege,
              artists: v.ar,
              album: v.al,
            };
            return v;
          });
          this.result = this.result.concat(re);
          this.isloading = false;
          this.arid = re[0].ar[0].id
          this.aridup()
        });
    },
    getMore() {
      this.isloading = true;
      this.page++;
      this.getResult();
    },
  },
};
</script>

<style lang="less" scoped>
.artist {
  width: 100%;
  height: 50px;
  background: skyblue;
}
.sr {
    margin-left: 2px;
    margin-top: 15px;
    padding-left: 39px;
    width: 180px;
    height: 30px;
    line-height: 30px;
    font-size: 17px;
    font-weight: 700;
    color: rgb(255, 255, 255);
    background-color: #3691f8;
    position: relative;
    border-radius:0 0 5px 0 ;
    .tg {
        position: absolute;
        top: -6px;
        left: 220px;
        width: 0;
        height: 0;
        border-top: 0px solid transparent;
        border-left: 45px solid #3691f8;
        border-bottom: 30px solid transparent;      
    }
}
.searchresult {
  margin-bottom: 50px;
}
.getmore {
  display: block;
  width: 150px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  margin: 10px auto;
  color: rgb(66, 64, 64);
}
.getmore:hover {
  color: #539df1;
}
</style>
